"use client";
import { useState, useEffect } from "react";
import { RefreshCw, ArrowRight } from "lucide-react";

const API_URL = "http://localhost:8080/api";

type MatchedOrder = {
  id: number;
  seller_price: number;
  buyer_price: number;
  seller_qty: number;
  buyer_qty: number;
  matched_qty: number;
  seller_time: string;
  buyer_time: string;
  seller_date: string;
  buyer_date: string;
  incoming_time: string;
  outgoing_time: string;
  time_taken: string;
  status: string;
  transaction_type: number;
  buyer_user_id: number;
  seller_user_id: number;
  buyer_transaction_id: string;
  seller_transaction_id: string;
  project_id: number;
  buyer_order_id: number;
  seller_order_id: number;
  is_multi_match: boolean;
};

export default function MatchedOrdersPage() {
  const [matchedOrders, setMatchedOrders] = useState<MatchedOrder[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  const fetchMatchedOrders = async () => {
    setLoading(true);
    setError(null);

    try {
      const response = await fetch(`${API_URL}/matched-orders`);
      if (!response.ok) {
        throw new Error("Failed to fetch matched orders");
      }
      const data = await response.json();
      setMatchedOrders(data || []);
    } catch (err) {
      setError(err instanceof Error ? err.message : "Unknown error occurred");
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchMatchedOrders();
  }, []);

  const formatDate = (dateStr: string) => {
    try {
      const date = new Date(dateStr);
      return date.toLocaleDateString('en-US', {
        month: '2-digit',
        day: '2-digit',
        year: 'numeric'
      });
    } catch {
      return dateStr;
    }
  };

  const formatTime = (timeStr: string) => {
    try {
      return timeStr.substring(0, 8);
    } catch {
      return timeStr;
    }
  };

  const getTransactionType = (type: number) => {
    switch (type) {
      case 0:
        return "Cash";
      case 1:
        return "Crypto";
      case 2:
        return "Both";
      default:
        return "Unknown";
    }
  };

  const handleViewDetails = (buyerOrderId: number) => {
    if (buyerOrderId && buyerOrderId > 0) {
      window.location.href = `/buyer-details?buyer_id=${buyerOrderId}`;
    }
  };

  return (
    <div className="min-h-screen bg-gray-50 py-8 px-4 sm:px-6 lg:px-8">
      <div className="max-w-7xl mx-auto">
        {/* Header */}
        <div className="mb-6 flex items-center justify-between">
          <h1 className="text-3xl font-bold text-gray-900 flex items-center gap-2">
            <span className="text-green-600">âœ“</span> All Matched Orders
          </h1>

          <button
            onClick={fetchMatchedOrders}
            disabled={loading}
            className="flex items-center gap-2 px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-all disabled:opacity-50 shadow-sm"
          >
            <RefreshCw size={20} className={loading ? "animate-spin" : ""} />
            <span className="font-medium">Refresh</span>
          </button>
        </div>

        {loading && (
          <div className="text-center py-12">
            <RefreshCw className="animate-spin mx-auto mb-4 text-blue-600" size={48} />
            <p className="text-gray-600">Loading matched orders...</p>
          </div>
        )}

        {error && (
          <div className="bg-red-50 border border-red-200 p-6 text-center rounded-lg">
            <p className="text-red-600 font-semibold">{error}</p>
          </div>
        )}

        {!loading && !error && matchedOrders.length === 0 && (
          <div className="bg-yellow-50 border border-yellow-200 p-6 text-center rounded-lg">
            <p className="text-yellow-800 font-semibold">No matched orders found</p>
          </div>
        )}

        {!loading && !error && matchedOrders.length > 0 && (
          <div className="bg-white rounded-lg shadow-md border border-gray-200 overflow-hidden">
            <div className="overflow-x-auto">
              <table className="w-full text-left border-collapse">
                <thead>
                  <tr className="border-b-2 border-gray-300 bg-gray-100">
                    <th className="px-4 py-3 text-xs font-bold text-gray-700">Transaction ID</th>
                    <th className="px-4 py-3 text-xs font-bold text-gray-700">Project ID</th>
                    <th className="px-4 py-3 text-xs font-bold text-gray-700">Type</th>
                    <th className="px-4 py-3 text-xs font-bold text-gray-700">Buyer ID</th>
                    <th className="px-4 py-3 text-xs font-bold text-gray-700 bg-blue-50">Buyer's Order</th>
                    <th className="px-4 py-3 text-xs font-bold text-gray-700">Seller's Order</th>
                    <th className="px-4 py-3 text-xs font-bold text-gray-700">Matched Qty</th>
                    <th className="px-4 py-3 text-xs font-bold text-gray-700">Time Taken</th>
                    <th className="px-4 py-3 text-xs font-bold text-gray-700">Status</th>
                    <th className="px-4 py-3 text-xs font-bold text-gray-700">Details</th>
                  </tr>
                </thead>
                <tbody>
                  {matchedOrders.map((order, idx) => (
                    <tr
                      key={order.id}
                      className={`${
                        idx % 2 === 0 ? "bg-white" : "bg-gray-50"
                      } hover:bg-blue-50 transition-colors border-b border-gray-100`}
                    >
                      <td className="px-4 py-3 text-sm text-gray-800 font-medium">
                        {order.buyer_transaction_id}
                      </td>
                      <td className="px-4 py-3 text-sm text-gray-800">
                        {order.project_id}
                      </td>
                      <td className="px-4 py-3 text-sm text-gray-800">
                        <span className="px-2 py-1 bg-gray-200 rounded text-xs">
                          {getTransactionType(order.transaction_type)}
                        </span>
                      </td>
                      <td className="px-4 py-3 text-sm text-gray-800 font-medium">
                        {order.buyer_user_id}
                      </td>

                      {/* Buyer's Order Section */}
                      <td className="px-4 py-3 text-sm bg-blue-50">
                        <div className="space-y-1">
                          <div className="text-xs text-gray-600">
                            <span className="font-semibold">{order.buyer_date}</span>
                            <span className="ml-1">{formatTime(order.buyer_time)}</span>
                          </div>
                          <div className="text-xs">
                            <span className="text-gray-700">Price: </span>
                            <span className="font-semibold">${order.buyer_price.toFixed(2)}</span>
                          </div>
                          <div className="text-xs">
                            <span className="text-gray-700">Qty: </span>
                            <span className="font-semibold">{order.buyer_qty}</span>
                          </div>
                        </div>
                      </td>

                      {/* Seller's Order Section */}
                      <td className="px-4 py-3 text-sm">
                        <div className="space-y-1">
                          <div className="text-xs text-gray-600">
                            <span className="font-semibold">{order.seller_date}</span>
                            <span className="ml-1">{formatTime(order.seller_time)}</span>
                          </div>
                          <div className="text-xs">
                            <span className="text-gray-700">Price: </span>
                            <span className="font-semibold">${order.seller_price.toFixed(2)}</span>
                          </div>
                          <div className="text-xs">
                            <span className="text-gray-700">Qty: </span>
                            <span className="font-semibold">{order.seller_qty}</span>
                          </div>
                        </div>
                      </td>

                      <td className="px-4 py-3 text-sm font-bold text-green-700">
                        {order.matched_qty}
                      </td>

                      <td className="px-4 py-3 text-sm text-gray-600">
                        {order.time_taken}
                      </td>

                      <td className="px-4 py-3 text-sm">
                        <span className="inline-block px-3 py-1 bg-green-100 text-green-800 rounded-full text-xs font-semibold border border-green-300">
                          {order.status}
                        </span>
                      </td>

                      <td className="px-4 py-3 text-sm">
                        <button
                          onClick={() => handleViewDetails(order.buyer_order_id)}
                          disabled={!order.buyer_order_id || order.buyer_order_id === 0}
                          className={`flex items-center gap-1 px-3 py-2 rounded transition-all ${
                            order.buyer_order_id && order.buyer_order_id > 0
                              ? "bg-blue-600 text-white hover:bg-blue-700 cursor-pointer"
                              : "bg-gray-300 text-gray-500 cursor-not-allowed"
                          }`}
                          title={
                            order.buyer_order_id && order.buyer_order_id > 0
                              ? `View details for Buyer Order #${order.buyer_order_id}`
                              : "Buyer Order ID not available"
                          }
                        >
                          <ArrowRight size={16} />
                          <span className="text-xs font-semibold">
                            {order.buyer_order_id > 0 ? order.buyer_order_id : "N/A"}
                          </span>
                        </button>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>

            {/* Footer */}
            <div className="bg-gray-50 px-4 py-3 border-t border-gray-200">
              <p className="text-sm text-gray-600">
                Showing <span className="font-semibold">{matchedOrders.length}</span> matched order(s)
              </p>
            </div>
          </div>
        )}
      </div>
    </div>
  );
}